<template>
  <div class="home grid grid-cols-1 gap-4 mt-20">
    <div class="p-6 rounded-lg border-4 border-opacity-75">
        <div class="text-xl mb-3">PPYB Formatter</div>
        <div class="p-field">
             <label for="fileUpload">File</label>
              <p-fileupload name="demo[]" id="fileUpload" :customUpload="true" ref="fileUpload"
                :showUploadButton="false" :showCancelButton="false" :fileLimit="1"
                @uploader="uploader" :auto="true">
                <template #empty>
                  <p>Drag and drop files to here to upload.</p>
                </template>
              </p-fileupload>
            </div>
        <div class="p-fluid">
            <div class="p-field">
                <label for="Judul">Judul</label>
                <p-inputtext id="Judul" v-model="Judul"/>
            </div>
            <div class="p-field">
                <label for="JumlahPax">Jumlah Pax</label>
                <p-inputnumber id="JumlahPax" v-model="JumlahPax"/>
            </div>
            <div class="p-field">
                <label for="JumlahPembagian">Jumlah Pembagian</label>
                <p-inputnumber id="JumlahPembagian" v-model="JumlahPembagian"/>
            </div>
        </div>
        <div class="flex justify-center">
          <p-button label="Proses" class="p-button-raised p-button-rounded w-full xl:w-80"
            @click="prosesFile" />
        </div>
    </div>
  </div>
</template>
<script>

import { ref } from 'vue';
import xlsx from 'xlsx';

export default {
  name: 'Home',
  setup() {
    const JumlahPax = ref(110);
    const JumlahPembagian = ref(5);
    const Judul = ref(null);
    const fileExcel = ref();
    const fileUpload = ref();

    const uploader = (event) => {
      fileExcel.value = event;
    };
    const result = ref([]);
    const calcFile = (file) => {
      file.forEach((row, index) => {
        console.log(row);
        const objFirst = {
          Name: row.Name,
          UoM: row.UoM,
          SumOfPax:  JumlahPembagian.value,
          SumOfGramasi: Math.round(row.Total/JumlahPax.value * 20),
          SumOfComp: JumlahPembagian.value * Math.round(row.Total/JumlahPax.value * 20)
        }
        result.value.push(objFirst);
        const objSecond = {
          Name: row.Name,
          UoM: row.UoM,
          SumOfPax:  Math.ceil((row.Total/objFirst.SumOfGramasi)-objFirst.SumOfPax),
          SumOfGramasi: row.Total%objFirst.SumOfComp,
          SumOfComp: (Math.ceil((row.Total/objFirst.SumOfGramasi)-objFirst.SumOfPax)) * (row.Total%objFirst.SumOfComp)
        }
        result.value.push(objSecond);
      });
      console.log(result.value)
    };
    const updateFile = async () => {
      // const readerExistFile = new FileReader();
      // readerExistFile.onload = (event) => {
      //   const data = event.target.result;
      //   const workbook = xlsx.read(data, { type: 'binary' });
      //   console.log(workbook);
      //   // const ws = workbook.Sheets[wb.SheetNames[0]];

      //   // /* change A1 to the label "issue1124" */
      //   // if(!ws["A1"]) ws["A1"] = {};
      //   // ws["A1"].t = "s";
      //   // ws["A1"].v = "issue1124";

      //   // /* save file */
      //   // XLSX.writeFile(wb, "issue1124.xlsx");

      // };
      // readerExistFile.readAsBinaryString(Template);
      // const response = await fetch('Template.xlsx');
      // const data = await response.blob();
      // const reader = new FileReader();
      // reader.onload = (event) => {
      //   console.log(event);
      // };
      // reader.readAsBinaryString(data);

      // const workbook = xlsx.readFile('Template.xlsx');
      //   console.log(workbook);
      const animalWS = xlsx.utils.json_to_sheet(result.value);
      console.log(animalWS);
      const wb = xlsx.utils.book_new();
      console.log(wb);
      xlsx.utils.book_append_sheet(wb, animalWS, 'Sheet1');
      xlsx.writeFile(wb, 'book.xlsx', { type: 'file' });
    }
    const prosesFile = () => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const data = event.target.result;
        const workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(async (sheetName) => {
          const XL_row_object = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheetName]);
          await calcFile(XL_row_object);
          await updateFile();
        });
      };
      reader.onerror = (event) => {
        console.error(event.target.error.code);
      };
      reader.readAsBinaryString(fileExcel.value.files[0]);

      // const workbook = xlsx.read(, { type: 'binary' });
      // console.log(workbook);
    };
    return {
      JumlahPax,
      JumlahPembagian,
      Judul,
      uploader,
      prosesFile,
      fileUpload,
    };
  },
};
